import React from 'react'

export default function WorkCard(props) {
    return (
        <div className="cont">
            <div className="left">
                <img src={props.data.image} />
            </div>
            <div className="right">
                <a href={props.data.link} target="_blank"> <h2>{props.data.title}</h2> </a>
                <p>{props.data.description}</p>
            </div>
            <style jsx>{`
                .cont{
                    width: 100%;
                    display: grid;
                    grid-template-columns: 60% 40%;
                    margin: 0;
                    padding: 0;
                }
                .left img {
                    max-height: 400px;
                    width: 100%;
                }
            `}</style>
        </div>
    )
}
